.cursor {
	width: 20px;
	height: 20px;
	background: #fff;
	position: fixed;
	border-radius: 50%;
	top: 0;
	left: 0;
	z-index: 2;
	pointer-events: none;
	transform: translate(-50%, -50%);
	transition: transform 0.3s;
	mix-blend-mode: difference;
}
